<template>
    <v-container>
        <v-layout>
            <v-flex sm12>
                <v-card xs12 sm6 offset-sm3>
                    <v-img
                        src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
          height="200px" >
                    </v-img>
                    <v-card-title>
                        {{objetoProducto.title}}
                    </v-card-title>
                    <v-card-text>
                        {{objetoProducto.descripcion}}
                    </v-card-text>
                    <v-card-text>
                        <v-chip color="green" text-color="white">Precio: ${{objetoProducto.precio}}</v-chip>
                    </v-card-text>
                    <v-card-actions>
                        <v-btn color="green"  @click="setCarrito(objetoProducto)" :to="{name: 'carrito'}">Agregar al carrito 

                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>


<script>
import axios from "axios"
import {mapMutations, mapState} from "vuex"
export default {
    name:'producto',
    data () {
        return {
            objetoProducto:{}
        }

    }
    ,
    methods: {
        ...mapMutations(['falso','inicializaCarrito','setProductos', 'setCarrito']),
        async getProductoById() {
          

          try {
            axios.create({headers: {'Access-Control-Allow-Origin':'*' }})
            let datos = await axios.get('http://apis-servicio.cernet.com.mx/product/'+this.$route.params.id)
            console.log(datos.data)
            this.objetoProducto = datos.data
          }catch (error) {
            console.log(error)
          }finally {
            
          }
      }
    },
    created() {
        this.getProductoById()
        this.falso(true)
        this.inicializaCarrito()
    },
}
</script>
